.recipe-card {
	width: 500px;
	min-width: 240px;
	margin: 1%;
	border: 1px solid lightgray;
	background: white;
	border-radius: 0 0 2px 2px;
	-webkit-box-shadow: 0 2px 2px 0 lightgray;
	        box-shadow: 0 2px 2px 0 lightgray;
	margin-bottom: 20px;
}

.recipe-card img {
	width: 100%;
}

.recipe-title {
	font-size: 25px;
	text-align: center;
	margin: 10px 0;
}

.card-content * {
    overflow: hidden;
}

.card-content {
	padding: 16px;
}

.card-content ul {
	list-style: circle;
}

.card-content button {
	background-color: #fff;
	border: 2px solid gray;
	padding: 5px;
	border-radius: 4px;
}

.delete-confirmation {
	padding: 0 10px 10px;
	margin: 15px 10px 10px;
	background-color: #ffec77;
	border-radius: 10px;
}

.delete-confirmation button {
	width: 50px;
	font-weight: bold;
	margin: 5px;
}

.delete-confirmation #accept-confirmation {
	margin-left: 5px;
	border: 2px solid red;
	color: red;
}

@media (max-width: 500px) {
	.recipe-card {
		width: 70%;
	}
}


